import { Component, ViewEncapsulation, OnDestroy, OnInit } from "@angular/core";
import { NgForm, FormBuilder, Validators, FormGroup, FormControl, AbstractControl } from "@angular/forms";
// import { Subscription } from "rxjs";
import { AuthService } from "../auth.service";
import { RegisterUserModal, LoginUserModal } from 'src/app/model/login.model';
import { CommonService } from 'src/app/common.service';
import { storeKey } from 'src/app/model/storageKey';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';




@Component({
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  encapsulation: ViewEncapsulation.None
})

export class loginComponent implements OnInit {

  public submited = false;
  setLoading = false;
  signInMode = "login"
  userLoginType = "EXIST"
  isUsername: { checking: boolean, timer: any } = {
    checking: false,
    timer: null
  }

  // private authStatusSub: Subscription;
  loginForm = this.fb.group({
    userName: ['', [Validators.required]],
    password: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(15)]]
  })
  // loginForm = this.fb.group({
  //   userName: ['abc@gmail.com', [Validators.required]],
  //   password: ['123', [Validators.required, Validators.minLength(3)]]
  // })
  registrationForm = this.fb.group({
    name: ['', [Validators.required]],
    userName: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(15)]],
    password: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(15)]],
    repassword: ['', [Validators.required, this.matchPassword()]],
    mobile: ['', [Validators.required, Validators.minLength(10), Validators.maxLength(10)]]
  })

  constructor(
    public authService: AuthService,
    private fb: FormBuilder,
    private _common: CommonService,
    private router: Router,
    private toastr: ToastrService
  ) {

  }

  ngOnInit() {
    document.body.classList.add("login-page", "hold-transition")
    // this.authStatusSub = this.authService.getAuthStatusListener().subscribe(
    //   authStatus => {
    //     this.isLoading = false;
    //   }
    // );
  }

  loginBtn() {
    console.log("loginBtn", this.loginForm)
    if (this.loginForm.valid) {
      this.setLoading = true

      let loginData: LoginUserModal = {
        userName: this.loginForm.value.userName,
        password: this.loginForm.value.password
      }
      this.authService.loginUser(loginData).subscribe((res: any) => {
        this.setLoading = false
        console.log("loginBtn_API", res)
        if (res.status == 1) {
          this.storeSessionData(res)
        }
        this.toastr[res.status == 1 ? 'success' : 'error'](res.message);
      })
    }
  }

  storeSessionData(res) {
    console.log("storeSessionData", res)
    this._common.setLocalData(storeKey.id, res.data._id)
    this._common.setLocalData(storeKey.userName, res.data.userName)
    this._common.setLocalData(storeKey.name, res.data.name)
    this._common.setLocalData(storeKey.mobileNumber, res.data.mobile)
    this._common.setLocalData(storeKey.token, res.token)

    this.navigateToHome()
  }

  navigateToHome() {
    this.router.navigate(['home'])
  }


  registerBtn() {
    console.log("registerBtn", this.registrationForm)
    if (this.registrationForm.valid) {
      this.setLoading = true
      let registerData: RegisterUserModal = {
        name: this.registrationForm.value.name,
        userName: this.registrationForm.value.userName,
        password: this.registrationForm.value.password,
        mobile: this.registrationForm.value.mobile
      }
      this.authService.registerUser(registerData).subscribe((res: any) => {
        console.log("registerBtn_API", res)
        this.setLoading = false
        if (res.status == 1) {
          this.userLoginType = 'NEWACC'
          this.signInMode = 'login'
        }
      })

    }
  }

  matchPassword() {
    return (control: AbstractControl): { [key: string]: any } | null => {
      console.log("matchPassword", control)
      if (control.parent) {
        let password: any = control.parent.controls['password']
        return (control.value == password.value) ? null : { 'matchError': true }
      } else {
        return null
      }
    }
  }

  // function emailDomain(domainName: string) {
  //   return (control: AbstractControl): { [key: string]: any } | null => {
  //     const email: string = control.value;
  //     const domain = email.substring(email.lastIndexOf('@') + 1);
  //     if (email === '' || domain.toLowerCase() === domainName.toLowerCase()) {
  //       return null;
  //     } else {
  //       return { 'emailDomain': true };
  //     }
  //   };
  // }

  validateUserName() {

    this.isUsername.checking = true
    if (this.isUsername.timer) {
      clearTimeout(this.isUsername.timer)
    }

    this.isUsername.timer = setTimeout(() => {


      let data = { userName: this.registrationForm.value.userName }



      if (data.userName.trim().length != 0) {
        this.registrationForm.controls.userName.setErrors({ 'hasWhiteSpace': false })
        this.authService.validateUserName(data).subscribe((res: any) => {
          this.setLoading = false
          this.isUsername.checking = false
          if (res.status == 1)
            // this.isUsername.isValid = !res.isExist
            this.registrationForm.controls.userName.setErrors(res.isExist ? { 'inValidUser': true } : null)

          console.log("loginBtn_API", res, this.registrationForm)
          // this.openToast()
          // this.storeSessionData(res)
        })
      } else {
        this.registrationForm.controls.userName.setErrors({ 'hasWhiteSpace': true })
      }
    }, 1000);

  }

  /**
 * Marks all controls in a form group as touched
 * @param formGroup - The form group to touch
 */
  private markFormGroupTouched(formGroup: FormGroup) {
    (<any>Object).values(formGroup.controls).forEach(control => {
      control.markAsTouched();

      if (control.controls) {
        this.markFormGroupTouched(control);
      }
    });
  }


  // login(form: NgForm) {
  //   this.submited = true
  //   if (form.valid) {
  //     console.log(form.value, "submited")
  //     // this.isLoading = true;
  //     this.authService.login(form.value.email, form.value.password);

  //   }
  // }

  // ngOnDestroy() {
  //   // this.renderer.removeClass(document.body, 'hold-transition');
  //   document.body.classList.remove("login-page", "hold-transition")
  //   // this.authStatusSub.unsubscribe();
  // }

  openToast() {
    console.log("Open Toast Called")
    // this.toastr.error('Hello world!');
    // this.toastr.info('Hello world!');
    // this.toastr.show('Hello world!');
    // this.toastr.warning('Hello world!');
  }


}