import { Component, OnInit } from '@angular/core';
import { CommonService } from '../common.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormArray, Validators, FormControl, FormGroup } from '@angular/forms';
import { TripserviceService } from '../tripservice.service';
import * as _ from "lodash";
import { storeKey } from '../model/storageKey';


@Component({
  selector: 'app-addmodifytrip',
  templateUrl: './addmodifytrip.component.html',
  styleUrls: ['./addmodifytrip.component.scss']
})
export class AddmodifytripComponent implements OnInit {

  addTripMode: boolean = false
  showCalculator: boolean = false
  // currentPage
  // tripDetailsObj: TripMemberModel

  // friendsList: FriendsModel[]
  friendsList: any = []
  friendsListFilter: any[]
  searchFriend
  userId
  currentTripData: any

  tripDataForm = this.fb.group({
    tripId: [null],
    tripName: ['', [Validators.required]],
    tripMembers: this.fb.array([])
  })

  trips: any[];

  constructor(
    private _common: CommonService,
    private router: Router,
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private _trip: TripserviceService
  ) { }

  ngOnInit() {
    let tripId = this.route.snapshot.paramMap.get('tripId');
    this._common.getLocalData(storeKey.id).then(id => {
      this.userId = id
    })
    this.tripDataForm.get('tripId').setValue(tripId ? tripId : null)
    this.tripDataForm.get('tripId').updateValueAndValidity()
    if (this.tripDataForm.value.tripId)
      this.getTripDetails();
    else
      this.getFriendsList(1)

  }

  getFriendsList(type) {
    let data = { type: type }
    this.friendsList = []
    this.friendsListFilter = []
    this.clearFormArray(this.tripMembersArray)
    this._trip.getFriendsList(data).subscribe((res: any) => {

      this.friendsList = res.friendsList.friendsDataInfo
      this.friendsListFilter = JSON.parse(JSON.stringify(this.friendsList))
      if (this.currentTripData) {
        this.setupTripModifyInfo()
      } else {
        this.friendsList.filter(item => {
          this.createMemberControl(false, item, [])
        })
      }
      console.log("getFriendsList", res, this.tripMembersArray)

    })
  }

  getTripDetails() {
    this.trips = []
    let tripId = this.tripDataForm.value.tripId
    this._trip.getTripDetails().subscribe((res: any) => {
      console.log("getTripDetails_API", res, this.tripDataForm)
      this.trips = res.result
      let index = _.findIndex(this.trips, function (o) {
        return o._id == tripId
      })
      this.currentTripData = index > -1 ? this.trips[index] : null
      this.getFriendsList(1)
    })
  }

  get tripMembersArray(): FormArray {
    return this.tripDataForm.get('tripMembers') as FormArray;
  }

  clearFormArray = (formArray: FormArray) => {
    while (formArray.length !== 0) {
      formArray.removeAt(0)
    }
  }

  setupTripModifyInfo() {
    console.log("setupTripModifyInfo", this.currentTripData)
    let data = { tripId: this.currentTripData._id }
    this.tripDataForm.get('tripName').setValue(this.currentTripData.tripName)
    this._trip.getTripMembersList(data).subscribe((res: any) => {
      console.log("getTripMembersList", res)
      // this.friendsList.filter(item => {
      //   this.createMemberControl(false, item)
      // })
      let filterData = res.result.members.filter(item => item._id != this.userId)
      this.friendsList = this.friendsList.concat(filterData)
      console.log("this.friendsList", this.friendsList)
      this.friendsList = this._common.removeDuplicate(this.friendsList, '_id')
      this.friendsList.filter(item => {
        let index = _.findIndex(res.result.members, function (o) {
          return o._id == item._id
        })
        this.createMemberControl((index > -1), item, this.checkIfInvolved(item._id))

        // if (index > -1) {
        //   // this.friendsList.push(item)
        //   this.createMemberControl(false, item)
        // }
        // else {
        //   this.createMemberControl(true, item)
        // }
      })
      // this.friendsList = this.friendsList.concat(tempArray)
      this.friendsListFilter = JSON.parse(JSON.stringify(this.friendsList))

      // res.result.members.filter(added => {
      //   // this.addSelectedMember(added)
      // })

    })
  }
  checkIfInvolved(memberId): Array<any> {
    console.log("checkIfInvolved", memberId, this.currentTripData)
    let involvedTrips = []
    this.currentTripData.tripExpense.filter(expense => {
      // expense.tripDetails.members.filter(member => {

      // })
      let index = _.findIndex(expense.tripDetails.members, function (o) {
        return o._id == memberId && (o.havetopay && o.finalAmount.toString() != '0')
      })
      if (index > -1)
        involvedTrips.push(expense)
    })
    return involvedTrips
  }

  switchSelection(control: FormControl) {
    console.log("switchSelection", this.tripMembersArray)
    control.get('isChecked').setValue(!control.value.isChecked)
  }

  createMemberControl(isChecked: boolean, item: any, isInvolvedArray: Array<any>): void {
    let control: FormGroup = this.fb.group({
      isChecked: [isChecked, [Validators.required]],
      memberData: [item, [Validators.required]],
      isInvolvedArray: [isInvolvedArray, [Validators.required]]
    })
    this.tripMembersArray.push(control)
  }

  navigateToTrips() {
    this.router.navigate(['home'])
  }


  saveTrip() {
    let data = {
      tripId : this.tripDataForm.value.tripId,
      tripName: this.tripDataForm.value.tripName,
      totalTripExpenseCost: null,
      members: []
    }
    this.tripDataForm.value.tripMembers.filter(item => {
      if (item.isChecked)
        data.members.push(item.memberData._id)
    })
    console.log("this.tripDataForm", this.tripDataForm, data)
    this._trip.AddTrip(data).subscribe((res: any) => {
      console.log("AddTrip", res)
      this.navigateToTrips()
    })
  }



  filterFriend(event) {
    console.log("filterFriend", event)
    this.friendsList = this.friendsListFilter.filter(item => (item.name).toLowerCase().includes(event.toLowerCase()))
  }

  // saveTrip() {
  //   console.log("saveTrip", this.tripDataForm)
  //   this.AddTrip()
  // }


}
