import { Component, OnInit } from '@angular/core';
import { TripDetailsModel, TripModel, TripMemberModel, FriendsModel } from '../model/calculator.model';
import { CommonService } from '../common.service';
import { Router } from '@angular/router';
import * as _ from "lodash";
import { TripserviceService } from '../tripservice.service';
import { storeKey } from '../model/storageKey';


@Component({
  selector: 'app-trip',
  templateUrl: './trip.component.html',
  styleUrls: ['./trip.component.scss']
})
export class TripComponent implements OnInit {

  constructor(
    private _common: CommonService,
    private router: Router,
    private _trip: TripserviceService
  ) { }

  userId
  trips: any[];

  ngOnInit() {
    this.getTripDetails();
    this._common.getLocalData(storeKey.id).then(id => {
      this.userId = id
    })
  }

  //To Navigate to Add/Edit Trips Page.
  editTrip(tripItem?) {
    this.router.navigate(tripItem ? ['addmodifytrip', tripItem.tripId] : ['addmodifytrip'])
  }

  //Navigate to Expense Page Where all expense of trips will be listed.
  showTripDetails(trip) {
    this.router.navigate(['expense', trip.tripId])
  }

  //Navigate to Friends Page Where you can manage friends.
  friendsPage() {
    console.log("Navigate to friends page")
    this.router.navigate(['friends'])
  }

  deleteTrip(tripItem) {
    console.log("deleteTrip", tripItem)
    let tripData = {
      tripId: tripItem.tripId
    }
    this._trip.deleteTrip(tripData).subscribe((res: any) => {
      console.log("deleteTrip_Api", res)
      this.getTripDetails()
    })
  }

  // ----------------------------------------------------------------------------------------------------
  getTripDetails() {
    this._common.setLoading(true)
    let addTrip
    this.trips = []
    this._trip.getTripDetails().subscribe((res: any) => {
      console.log("getTripDetails_API", res)
      res.result.filter(item => {
        addTrip = {
          tripId: item._id,
          tripName: item.tripName,
          members: item.members,
          totalTripExpenseCost: this.calculateExpenseDetails(item),
          tripExpense: item.tripExpense,
          creatorID: item.creatorID
        }
        this.trips.push(addTrip)
      })
      this._common.setLoading(false)

    })
  }

  calculateExpenseDetails(tripItem) {
    let totalCost: number = 0
    tripItem.tripExpense.filter(item => {
      console.log("calculateExpenseDetails", item)
      let num: any = item.tripDetails.expenseCost
      totalCost = totalCost + parseFloat(num)
    })
    return totalCost
  }
}
